import React, { useState, useEffect } from "react";
import { useAuth } from "../context/auth";
import { Radio } from "antd";
import { useCart } from "../context/cart";
import axios from "axios";
import Layout from "./../components/Layout/Layout";
import {toast} from "react-toastify";
import "../styles/Homepage.css";
import { Prices } from "./Prices.js";
import { useNavigate } from "react-router-dom";
import HeroSection from "./HeroSection";

const HomePage = () => {
    const navigate = useNavigate();
    const [cart, setCart] = useCart();
  const [auth, SetAuth] = useAuth();
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
    const [checked, setChecked] = useState([]);
    const [radio, setRadio] = useState([]);
    const [total, setTotal] = useState(0);
    const [page, setPage] = useState(1);
    const [loading, setLoading] = useState(false);

  //get all cat
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get("/api/v1/catagory/get-catagories");
      if (data?.sucess) {
        setCategories(data?.cat);
      }
    } catch (error) {
      console.log(error);
    }
  };
  
  useEffect(() => {
    getAllCategory();
    const scrollBtn = document.querySelector('#scrolldown');
    const myDiv = document.querySelector('#all-products');

scrollBtn.addEventListener('click', () => {
  // Scroll to the element
  myDiv.scrollIntoView({ behavior: 'smooth' });
});
  }, []);
  //get products
  const getAllProducts = async () => {
    try {
      // setLoading(true);
      const { data } = await axios.get(`/api/v1/product/get-product`);
      // setLoading(false);
      setProducts(data.products);
    } catch (error) {
      // setLoading(false);
      console.log(error);
    }
  };
  useEffect(() => {
    getAllProducts();
  }, []);
    //getTOtal COunt
    const getTotal = async () => {
      try {
        const { data } = await axios.get("/api/v1/product/product-count");
        setTotal(data?.total);
      } catch (error) {
        console.log(error);
      }
    };

    useEffect(() => {
      if (page === 1) return;
      loadMore();
    }, [page]);
    //load more
    const loadMore = async () => {
      try {
        setLoading(true);
        const { data } = await axios.get(`/api/v1/product/product-list/${page}`);
        setLoading(false);
        setProducts([...products, ...data?.products]);
      } catch (error) {
        console.log(error);
        setLoading(false);
      }
    };
    
    // filter by cat
    const handleFilter = (value, id) => {
      let all = [...checked];
      if (value) {
        all.push(id);
      } else {
        all = all.filter((c) => c !== id);
      }
      setChecked(all);
    };
    useEffect(() => {
      if (!checked.length || !radio.length) getAllProducts();
    }, [checked.length, radio.length]);
    
    useEffect(() => {
      if (checked.length || radio.length) filterProduct();
    }, [checked, radio]);

    //get filterd product
    const filterProduct = async () => {
      try {
        const { data } = await axios.post("/api/v1/product/product-filters", {
          checked,
          radio,
        });
        setProducts(data?.products);
      } catch (error) {
        console.log(error);
      }
    };
  return (
    <Layout title={"ALl Products - Best offers "}>
      <div className="row mt-4">
        <HeroSection/>
        <div id="filters" className="col-md-2 home-filters">
          <h4 className="mt-2 text-center heading">Filters</h4>
           <br/> <h4 className="text-center">By Category</h4>
          <div className="d-flex flex-column align-center">
            <ul>
              {categories?.map((c) => (
                <li key={`cat-${c._id}`} className="d-flex">
                  <input
                    type="checkbox"
                    key={c._id}
                    onChange={(e) => handleFilter(e.target.checked, c._id)}
                    />
                  <label className="cat-filter-name ml-5" htmlFor={c._id}>{c.name}</label>
                </li>
              ))}
            </ul>
          </div>
          <h4 className="text-center mt-4">By Price</h4>
          <div className="d-flex flex-column mr-auto align-center">
          <Radio.Group onChange={e=>setRadio(e.target.value)}>
            {Prices?.map((p)=>
              <div key={`price-${p._id}`}>
                <Radio value={p.array}>{p.name}</Radio>
              </div>
            )}
          </Radio.Group>
          </div>
          <div className="d-flex flex-column">
            <button className="btn btn-danger" onClick={()=>window.location.reload()}>RESET FILTERS</button>
          </div>
        </div>
        <div className="col-md-10">
          <h1 className="text-center heading">All Products</h1>
          <div id="all-products" className="d-flex flex-wrap">
            {products?.map((p) => (
              <div key={p._id} className="card m-2" style={{ width: "18rem" }}>
                <img
                  src={`/api/v1/product/product-photo/${p._id}`}
                  className="card-img-top"
                  alt={p.name}
                  />
                <div className="card-body">
                  <h5 className="heading">{p.name}</h5>
                  <p className="card-text">
                    {p.description.substring(0,30)}...
                    </p>
                  <p className="card-text c-red">$ {p.price}</p>

                  <button onClick={()=>navigate(`/product/${p.slug}`)} className="btn btn-secondary m-2">More Details</button>
                  <button onClick={()=>{
                    setCart([...cart, p]);toast.success("Item added to Cart");
                    localStorage.setItem("cart", JSON.stringify(cart));
                  }} className="btn btn-secondary add-to-cart">Add To Cart</button>
                </div>
              </div>
            ))}
          </div>{" "}
          <div className="m-2 p-3">
            {products && products.length < total && (
              <button
                className="btn loadmore"
                onClick={(e) => {
                  e.preventDefault();
                  setPage(page + 1);
                }}
              >
                {loading ? (
                  "Loading ..."
                ) : (
                  <>
                    {" "}
                    Loadmore
                  </>
                )}
              </button>
            )}
          </div>

        </div>
      </div>
    </Layout>
  );
};

export default HomePage;
