import axios from "axios";
import React, { useState, useEffect } from "react";
import { toast } from "react-toastify";
import CategoryForm from "../../components/Forms/CategoryForm";
import Layout from "../../components/Layout/Layout";
import { useAuth } from "../../context/auth";
import AdminMenu from "./AdminMenu";

const CreateCategory = () => {
  const [auth] = useAuth();
  const [categories, setCategories] = useState([]);
  const [name, setName] = useState("");
  //handle Form
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/v1/catagory/create-catagory", {
        name,
      });
      if (data?.sucess) {
        toast.success(`${name} is created`);
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("somthing went wrong in input form");
    }
  };

  //get all cat
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get("/api/v1/catagory/get-catagories");
      if (data?.sucess) {
        setCategories(data.cat);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something wwent wrong in getting catgeory");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);
 //delete category
  const handleDelete = async (pId) => {
    try {
      const { data } = await axios.delete(
        `/api/v1/catagory/delete-catagory/${pId}`
      );
      if (data.sucess) {
        toast.success(`category is deleted`);
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      toast.error("Somtihing went wrong");
    }
  };
  return (
    <Layout title={"Create Catagory"}>
      {auth.user.role === 1 ? (
        <>
          <div className="container-field">
            <div className="row">
              <div className="col-md-3">
                <h3>Admin Panel</h3>
                <AdminMenu />
              </div>
              <div className="col-md-9">
                <h1>Manage Category</h1>
                <div className="p-3 w-50">
                  <CategoryForm
                    handleSubmit={handleSubmit}
                    value={name}
                    setValue={setName}
                  />
                </div>
                <div className="w-75">
                  <table className="table">
                    <thead>
                      <tr>
                        <th scope="col">Name</th>
                        <th scope="col">Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {categories?.map((c) => (
                          <tr key={c._id}>
                            <td key={c._id}>{c.name}</td>
                            <td>
                              <button
                                className="btn btn-danger ms-2"
                                onClick={() => {
                                  handleDelete(c._id);
                                }}
                              >
                                Delete
                              </button>
                            </td>
                          </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
                
              </div>
            </div>
          </div>
        </>
      ) : (
        <div>sdkfasldf</div>
      )}
    </Layout>
  );
};

export default CreateCategory;
